import * as React from 'react';

interface Props {
    // tslint:disable-next-line:no-any
    onSearchTermChange?: any;
    // tslint:disable-next-line:no-any
    showDropdown?: any;
    // tslint:disable-next-line:no-any
    hideDropdown?: any;
    term?: string;
}

export default class SearchBar extends React.Component<Props> {
    constructor(props: Props) {
        super(props);
    }

    render() {
        return (
            <form className="uk-button-group bolder uk-width-1-1">
                <input
                    onFocus={() => this.props.showDropdown()}
                    onBlur={() => this.props.hideDropdown()}
                    value={this.props.term}
                    onChange={(event) => {
                        this.handleInputChange(event.target.value);
                    }}
                    placeholder="Search on unizonn"
                    autoComplete="off"
                    className="uk-input uk-form-width-large"
                    type="search"
                    name="search[q]"
                    id="search_q"
                />
                <button name="button" type="submit" className="uk-button uk-button-default uk-text-center">
                    <i className="fa fa-search" />
                </button>
            </form>
        );
    }

    // tslint:disable-next-line:typedef
    handleInputChange(term) {
        this.props.onSearchTermChange(term);
    }
}


